body {
  padding: 0;
  margin: 0 0;
  font-family: sans-serif;
}
.navbar li {
  display: inline-block;
  text-align: center;
}

.itemLeft {
  width: 15%;
}

.itemCenter {
	width: 70%;
}

.itemRight {
  width: 15%;
}

.navbar {
  background: #2f533d;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

li {
  color: white;
  padding: 15px 0;
}

body {
  font-size: 24px;
}

.header{
	background-color: rgb(8, 7, 90);
	position: relative;
	width: 100%;
}

h1{
	text-align: center;
	font-size: 50px;
	font-weight: bold;
	padding: 50px 0 10px 0; 
	color: white
}

h2{
	text-align: center;
	font-size: 30px;
	padding: 0px 150px 30px 150px;
	color: whitesmoke
}

.card {
    width: 360px;
    height: 212px;
    margin: 15px 45px;
    border: 4px solid white;
  }
  
.footer{
	background-color: #2f533d;
	width: 100%;
	position: relative;
}

.footer p{
	color: white;
	text-align: center;
	padding: 20px 0;
	font-size: 25px;
}
.wrapper {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 25px 0 50px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
}

